'use client';

import MobileFrame from '@/component/layout/MobileFrame';
import Image from 'next/image';
import result1 from '@/../public/4.result/result-1.png';
import result2 from '@/../public/4.result/result-2.png';
import result3 from '@/../public/4.result/result-3.png';

import { usePsyStore, getMBTIResult, getMBTIProfile } from '@/app/store/store';

export default function ResultPage() {
  const mbti = usePsyStore((s) => s.mbti);
  const mbtiCode = getMBTIResult(mbti);
  const profile = getMBTIProfile(mbtiCode);

  const playAgain = () => {
    window.location.reload();
  };

  return (
    <MobileFrame>
      <div className="flex flex-col items-center gap-4 px-4 text-center">

        <h2 className="text-2xl font-bold">{profile.name}</h2>
        <p className="text-gray-700 text-lg">{profile.description}</p>

        {/* Optional: 不同類型對應不同圖片 */}
        {mbtiCode === 'INFP' && <Image src={result1} alt="INFP" />}
        {mbtiCode === 'INFJ' && <Image src={result2} alt="INFJ" />}
        {mbtiCode === 'ISFP' && <Image src={result3} alt="ISFP" />}
        
        {/* 再玩一次 */}
        <div
          className="mt-6 bg-[#89BCFF] w-full rounded-full text-white 
          py-[16px] text-sm flex justify-center items-center font-medium 
          shadow-[0px_4px_0px_1px_#1098EC] cursor-pointer hover:translate-y-0.5 transition"
          onClick={playAgain}
        >
          再玩一次
        </div>
      </div>
    </MobileFrame>
  );
}
